.docs article {
  padding: 25px 55px 50px 50px;
  margin-left: 275px;

  ul {
    font-size: 14px;
  }

  .content-list ul li {
    margin: 8px;
    line-height: 1.65;
  }

  p {
    font-size: 15px;
    line-height: 1.80;
  }

  > h1 {
    font-size: 42px;
    font-weight: 100;
    margin: 7px 0 0;
  }

  h1 + ul {
    margin: 37px 0 0;
    padding: 0;
    list-style: none;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.5;
    padding-bottom: 50px;
    border-bottom: 1px solid $color__faint;
    -webkit-font-smoothing: antialiased;
    li {
      &:before {
        content: "# ";
        margin-right: .25em;
        color: $color__salmon;
        opacity: .3;
      }
      a {
        text-decoration: none;
      }
    }
  }

  li > ul {
    font-size: 15px;
    font-weight: 400;
    list-style: none;
  }

  h2:first-of-type {
    margin-top: 15px;
  }

  h2 {
    font-size: 30px;
    font-weight: 400;
    margin-top: 47px;
    position: relative;

    a,
    a:hover {
      color: $color__gray;
      text-decoration: none;
    }
    a:before {
      content: "#";
      margin-left: -25px;
      position: absolute;
      font-size: 28px;
      top: 3px;
      color: $color__salmon;
      opacity: .6;
    }
  }

  h3 {
    font-size: 24px;
    font-weight: 400;
    margin: 37px 0 7px;
  }

  h4 {
    font-size: 16px;
    font-weight: 700;
    margin-top: 35px;
  }

  a {
    text-decoration: underline;
    &:hover {
      color: darken($color__salmon, 10%);
    }
  }

  blockquote a:hover {
    color: lighten($color__salmon, 25%);
  }

  table {
    border-collapse: collapse;
    width: 100%;
    font-size: 14px;
    th,
    td {
      border: 1px solid $color__lighter_gray;
      padding: 10px;
      text-align: left;
    }
    th {
      font-size: 16px;
    }
  }

  blockquote {
    border: none;
  }
}

.docs blockquote.has-icon {
  position: relative;

  &.video, &.laracast {
    background: rgba(103, 58, 183, 0.62);
  }

  &.tip {
    background: #64B5F6;
  }

  p {
    padding-left: 40px;
  }

  .flag {
    position: absolute;
    width: 30px;
    left: 15px;
    top: 10px;
    svg {
      width: 24px;
      height: 24px;
    }
  }
}

.docs .svg svg {
  width: 24px;
}

.autocomplete-wrapper .h1 {
  font-size: 16px;
  font-weight: bold;
}

.autocomplete-wrapper .h2 {
  font-size: 16px;
  display: inline-block;
}

.autocomplete-wrapper .h2 .hash {
  color: #F8A2A9;
}

.autocomplete-wrapper .h3 {
  font-size: 16px;
  display: inline-block;
}

.autocomplete-wrapper .h4 {
  font-size: 16px;
  display: inline-block;
}

.autocomplete-wrapper .content {
  font-size: 13px;
  background-color: rgba(238, 238, 238, 0.35);
  padding: 10px;
  border-radius: 3px;
  margin-left: 3px; // Needed to align the text with h. div text
  margin-top: 14px;
}